				    url = action == "delete" ? '/api/InviteFamilyMember/DeleteFamilyMember' : '/api/InviteFamilyMember/ResendInvitationFamilyMember',
				    data = { memberid: id },
				    dataType = 'json',
				    request = ajaxRequest(type, url, data, dataType);
